@use "global" as *;


.cta {
  display: block;
  padding-top: rem(96);
  padding-bottom: rem(92);
  position: relative;

  @include mq(md) {
    padding-top: rem(48);
    padding-bottom: rem(48);
  }
}

.cta:hover .cta__bg img {  // hover
  transform: scale(1.05);
}

.cta__leaf {  // あしらい (葉っぱ)
  position: absolute;
  bottom: calc(100% - rem(30));
  left: calc(50% - min(49.6vw, 44.9rem));  // *位置を制御

  @include mq(md) {
    bottom: calc(100% - rem(14));
  }
}

.cta__leaf img {
  aspect-ratio: 1.02 / 1;
  width: rem(252.18);

  @include mq(md) {
    width: rem(106.42);
  }
}

.cta__bg {
  display: block;
  // aspect-ratio: 1440 / 440;
  width: 100%;

  position: absolute;
  inset: 0;
  object-fit: cover;
  z-index: -1;
  overflow: hidden;
}

.cta__bg img {
  // aspect-ratio: 1440 / 440;
  width: 100%;
  height: 100%;
  object-fit: cover;
  transition: transform 0.25s ease-out;
}

.cta__inner.inner {
  max-width: rem(calc(488 + 30 * 2));  // 両端のpadding分を考慮

  @include mq(md) {
    max-width: rem(calc(345 + 24.5 * 2));  // 両端のpadding分を考慮
  }
}

.cta__top {

}

.cta__title {
  text-align: center;
  color: $color-white;
  leading-trim: both;
  text-edge: cap;
  font-family: $font-family-en-1;
  font-size: rem(61);
  font-weight: $bold;
  line-height: 1.0;
  letter-spacing: 0.04em;
  text-indent: 0.04em;
  text-transform: capitalize;
  margin: 0;

  @include mq(md) {
    font-size: rem(39);
  }
}

.cta__text {
  margin-top: rem(25);
  color: $color-white;
  text-align: center;
  leading-trim: both;
  text-edge: cap;
  font-size: rem(16);
  font-weight: $normal;

  @include mq(md) {
    font-size: rem(14);
  }
}

.cta__bottom {
  margin-top: rem(45);
  display: flex;
  justify-content: space-between;
  align-items: center;
  gap: rem(30.6);  // 念の為に指定

  padding-block: rem(24.5);
  padding-left: 9.2%;
  padding-right: 4.21%;
  border-top: 1px solid $color-white;
  border-bottom: 1px solid $color-white;

  @include mq(md) {
    margin-top: rem(30);
    gap: normal;

    padding-block: rem(24);
    padding-left: 2.46%;
    padding-right: 2.46%;
  }
}

.cta__left {
  display: grid;
  place-items: center;
}

.cta__left svg {
  @include mq(md) {
    max-width: rem(37);
  }
}

.cta__middle {
  margin-left: rem(-35);  // 位置調整

  @include mq(md) {
    margin-left: 0;
  }
}

.cta__middle-title {
  text-align: left;
  color: $color-white;
  leading-trim: both;
  text-edge: cap;
  font-family: $font-family-ja-1;
  font-size: rem(20);
  font-weight: $bold;
  line-height: 1.4;

  @include mq(md) {
    font-size: rem(18);
  }
}

.cta__middle-text {
  text-align: left;
  color: #EED5CE;
  leading-trim: both;
  text-edge: cap;
  font-size: rem(12);
  font-weight: $normal;
  line-height: 1.6;

  @include mq(md) {
    font-size: rem(12);
  }
}

.cta__right {
  display: grid;
  place-items: center;
  padding: rem(22.5);
  border: 1px solid $color-white;
  border-radius: 50%;

  @include mq(md) {
    flex-shrink: 0;
    padding: rem(15);
  }
}

.cta__right svg {
  aspect-ratio: 17.8 / 17;
  width: rem(17.8);
  display: grid;
  place-items: center;
}
